@import 'helpers/mixins';
.contact
{
    padding-top: 50px;
    min-height: 927.8px;
    @include backgroundImage("../../../images/overlay-bg.jpg",auto);
    .overley
    {
        @include Overley(#0078ff,0.7);
    }
    .contact-mf
    {
        margin-top: 4rem;
        .box-shadow-full
        {
            padding: 3rem;
            position: relative;
            background-color: #fff;
            margin-bottom: 3rem;
            z-index: 2;
            box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.06), 0 2px 5px 0 rgba(0, 0, 0, 0.2);

            .title-box-2
            {
                margin-bottom: 3rem;
                .title-left
                {
                    font-size: 2rem;
                    position: relative;
                    &:before
                    {
                        content:"";
                        position: absolute;
                        width:100px;
                        height: 3px;
                        background-color:#0078ff ;
                        bottom: -12px;
                    }
                }

              
            }
            #sendmessage
            {
                color: #0078ff;
                border: 1px solid #0078ff;
                display: none;
                text-align: center;
                padding: 15px;
                font-weight: 600;
                margin-bottom: 15px;
            }

            #errormessage
            {
                color: red;
                display: none;
                border: 1px solid red;
                text-align: center;
                padding: 15px;
                font-weight: 600;
                margin-bottom: 15px;
            }
            .validation
            {
                color: red;
                display: none;
                margin: 0 0 20px;
                font-weight: 400;
                font-size: 13px;
            }

            .title-left
            {
                position: relative;
                &:before
                {
                    content:"";
                    position: absolute;
                    width:100px;
                    height: 3px;
                    background-color:#0078ff ;
                    bottom: -12px;
                }
            }
            .lead
            {
                line-height: 1.5 !important;
            }

            .fa-map-marker-alt,
            .fa-phone-alt,
            .fa-envelope
            {
                margin-right: 10px;;
                color:#0078ff
            }
            ul
            {
                margin-top: 10px;
            }
            ul  li
            {
                padding-bottom:20px;
                font-size: 16px;
            }
            .socials 
            {
                padding: 1.5rem 0;
                display: inline-block;
            }

            .fa-facebook,
            .fa-instagram,
            .fa-twitter,
            .fa-pinterest-p
            {

                width: 40px;
                height: 40px;
                border-radius: 50%;
                line-height: 100px;
                padding:10px;
                background-color: #FFF;
                box-shadow: 0 0 0 3px #0078ff;
                transition: all 500ms ease;
                margin:-10px 15px 0 0;
                &:hover
                {
                    background-color: #0078ff;
                    color: #fff;
                    box-shadow: 0 0 0 3px #cde1f8;
                    transition: all 500ms ease;
                    cursor: pointer;
                }
            }

            .btn-primary
            {
                border-radius: 5rem !important;
                padding: .9rem 2.3rem;
                font-size: 1.2rem;
                background-color: #0078ff;
                color: #fff;
                border-color: #cde1f8;
                &:hover
                {
                    background-color: #0062d3;
                    color: #fff;
                    transition: all 500ms ease;
                    box-shadow: 0 0 0 4px #cde1f8;
                }
                
            }
           
        }
    }
}