@import 'helpers/mixins';
.work
{
    min-height: 657.6px;
    @include backgroundImage("../../../images/overlay-bg.jpg",auto);
    .overley
    {
        @include Overley(#0078ff,0.7);
    }   
    .author 
    {
        color:
        #fff;
        text-transform: uppercase;
        font-weight: 600;
        margin: 1rem 0;
        display: block;
        font-size: 1.4rem;
    }

    .testimonial-box
    {
        text-align: center;
    }
    .description
    {
        padding: 0 13rem;
        color:#FFF;
        margin-bottom: 20px;
        
    }
    .owl-state-outer
    {
        overflow: hidden;
    }
    .comit 
    {
        
        font-size: 2rem;
        color:
        #0078ff;
        background-color:
        #fff;
        width: 52px;
        height: 52px;
        display: block;
        margin: 0 auto;
        border-radius: 50%;
        line-height: 1.6;
    }
    img 
    {
        width: auto !important;
        margin: 0 auto;
        
    }
    .testimonial-box
    {
        margin-top: 200px;
    }
    .owl-dots
    {
        text-align: center;
        margin-top: 18px;
        span
        {
            width: 18px;
            height: 7px;
            margin: 5px 5px;
            background:
            #cde1f8;
            border: 0px solid
            #cde1f8;
            display: block;
            transition: all 0.6s ease-in-out;
            cursor: pointer;
        }
        .owl-dot.active span
        {
            background-color:
            #1B1B1B;
            width: 25px;    
        }
        
    }

   
}