.statistics
{
    min-height: 432.5px;
 
    @include backgroundImage("../../../images/counters-bg.jpg",auto);
    .overley
    {
        @include Overley(#0078ff,0.7);
    }    
    .counter-box
    {
        padding-top: 150px !important;
        color:#FFF;

        .counter-ico
        {
           
            margin-bottom: 16px;
            
            .fa-check,
            .fa-calendar,
            .fa-users,
            .fa-award
            {
               
                height: 60px;
                width: 60px;
                line-height: 1.8;
                box-shadow: 0 0 0 10px #cde1f8;
                border-radius: 50%;
                padding:15px
                        
            }
            
        }
        .counter-content
        {
            .counter
            {
                font-size: 24px;
                margin-bottom: 10px;
                
            }
            .counter-text
            {
                
                font-size: 16px;
            }
        }


    }

   

   
}