@import 'helpers/mixins';
@import 'helpers/_variables';
*
{
   font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
body 
{
   height: 6000px;
   
}
.navbar-toggler
{
   background-color: transparent !important;
   border: 1px solid transparent !important;
}
.navbar-toggler span
{
 
   background-color: #1B1B1B;
  
 
}
/*nav shit*/
.navbar-b
{
   
   transition: all .5s ease-in-out;
   padding-top: 20.008px;
   padding-bottom: 20.008px;
   box-shadow: 0px 6px 9px 0px rgba(0, 0, 0, 0.06) !important;

   .navbar-brand
   {
      font-size: $manFontSize + 9.6px;
      font-weight: 600;
      margin-bottom:10px ;
   }

   .nav-item 
   {
      padding:8px;
      font-weight:600;
      font-size: $manFontSize;
      margin-left: 5px !important;

      .nav-link
      {
         color:#FFF ;
      }

      .nav-link:after
      {
         content:"";
         margin-top:10px ;
         display:block;
         width:0%;
         height: 2px;
         background-color: #0078ff;
         transition: all 500ms ease;
      }
      .nav-link:hover:after
      {
         width: 35px;
      }
      .active2:after
      {
      
         width: 35px;
      
      }


   }


}
.bg-dark
{
  background-color: transparent !important;
  
}
.bg-white
{
  background-color: #fff !important;
}
/*end nav shit*/

/*start header*/
.header 
{
   @include backgroundImage('../../../images/intro-bg.jpg',1000px);

   .overley
   {
      @include Overley(black,0.6);
   }
  .content 
  {
     
     @include Center();

      h1
      {
         color:#FFF;
         font-size: 72px;
      }

      p
      {
         .text-slider-items
         {
            display: none;
         }
         color:#FFF;
         font-size: 40px;
         font-weight: 300;
         font-size: normal !important;
         text-align: center;
        
   
      }
  }

}
/*end header*/
