/*start  about*/
.about
{
   padding-top: 64px;
   .box-shadow-full
   {
      padding-top: 64px;
      padding-bottom: 64px;
      box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.06), 0 2px 5px 0 rgba(0, 0, 0, 0.2);
      background-color: #fff;

      .about-img img 
      {
         margin-left:50px;
      }

      .about-info
      {
         .title-s 
         {
            font-weight: 600;
            color: #1e1e1e;
            font-size: 1.1rem;
          }
         
      }
      .skill-mf
      {
         margin-left: 40px;;
         margin-top: 20px;
            
         .data
         {
            margin-top: 20px;
         }
      }

      .about-me
      {
         .title-left
         {
            font-weight: 300;
            font-size: 32px;
            &:after
            {
              content:"";
              margin-top:10px ;
              display:block;
              width:110px;
              height: 3px;
              background-color: #0078ff;
            }
         
            .info
            {
               padding: 150px !important;
            }
        
         }
      }


   }
}
/*End  about*/