.blog 
{
    img
    {
        vertical-align: middle;
    }
    padding-top: 50px;
    .text-info
    {
        h3
        {
           font-size: 3rem;
           font-weight: bold;
           text-transform: uppercase;
           color:#1e1e1e;
        }
        p
        {
           color:#4e4e4e;
           &:after
           {
              content:'';
              display: block;
              width:40px;
              height: 5px;
              background-color: #0078ff;
              margin: 15px auto;
              margin-bottom: 50px !important;
           }
        }
    }
    
    .card-blog
    {
        margin-bottom: 30px;;
        &:hover
        {
            cursor: pointer;
            
        }
        .card-img
        {
            .image-fluid
            {
                max-width: 100%;
                height: auto;
            }
        }

        .card-body
        {
            position: relative;
            .card-category-box
            {
                position: absolute;
                text-align: center;
                top: -16px;
                left: 15px;
                right: 15px;
                line-height: 25px;
                overflow: hidden;
                
                .card-category
                {
                    display: inline-block;
                    color: #fff;
                    padding: 0 15px 2px;
                    overflow: hidden;
                    background-color: #0078ff;
                    border-radius: 4px;

                    .category
                    {
                        color: #fff;
                        text-transform: uppercase;
                        font-size: .7rem;
                        letter-spacing: .1px;
                        margin-bottom: 0;
                    }
                }
            }
            .card-title
            {
                font-size: 1.3rem;
                margin-top: .6rem;
            }
            .card-description
            {
                color: #4e4e4e;
            }

        }

        .card-footer
        {
           
           
            .post-author
            {
                display: inline-block;
                .author
                {
                    font-size: 16px;
                    margin-left: 4px;
                    &:hover
                    {
                        transition: all 0.5s ease-in-out;
                        color:#0078ff;
                        cursor: pointer;
                       
                    }
                }
            }
            .avatar
            {
                    width: 32px;
                    height: 32px;
                    margin-right: 4px;
                    overflow: hidden;
            }
            .rounded-circle
            {
                border-radius: 50% !important;
            }
            .post-date
            {
                    color: #4e4e4e;
                    float: right;
                    .min
                    {
                        margin-left: 5px;
                        
                    }
                    margin-top: 5px;
            }
            
        }
    }

   
}