.portfolio
{
    padding-top: 50px;
    .text-info
    {
        h3
        {
           font-size: 3rem;
           font-weight: bold;
           text-transform: uppercase;
           color:#1e1e1e;
        }
        p
        {
           color:#4e4e4e;
           &:after
           {
              content:'';
              display: block;
              width:40px;
              height: 5px;
              background-color: #0078ff;
              margin: 15px auto;
              margin-bottom: 50px !important;
           }
        }
    }

    .work-box
    {
        box-shadow: 0 13px 8px -10px rgba(0, 0, 0, 0.1);
        margin-bottom: 3rem;
        backface-visibility: hidden;
        background-color: #fff;
        a
        {
            
            color:#1e1e1e;
            transition: all 0.5s ease-in-out;
            text-decoration: none;
            background-color:  transparent;
          
        }
     
      
        &:hover img
        {

            transform: scale(1.3);
        }
     
      
        img
        {
            transform: scale(2.3);
            transition: all 1s;
            border-style: none;
        }
        .work-img
        {
            display: block;
            overflow: hidden;
        }
        .w-title
        {
            font-size: 18px;
            margin-left: 10px;
            font-weight: 600;
            margin-top: 43px;
        }
        .w-more
        {
            
            margin-left: 10px;
            padding-bottom: 30px;
            
        }
        .w-like
        {
            
           
            color:#0078ff;
            margin-top: 46px;
            font-size: 22px;
            margin-right: 10px;
         
            
        }
        .fa-plus
        {
            height: 40px;
            width: 40px;
            line-height: 40px;
            border:2px solid  #0078ff;
            border-radius: 50%;
            padding:8px
        }
        .work-content
        {
            background-color:#FFF;
            margin-top: -12px
        }
        .w-ctegory
        {
            color:#0078ff;
        }
    }
}