@import 'helpers/../variables';
@mixin Overley($rgba,$opacity)
{
    position: absolute;
    left:0;
    top:0;
    width:100%;
    height: 100%;
    background-color: rgba($rgba,$opacity);
    color:#FFF;;
}
@mixin backgroundImage($src,$height)
{
   background:url($src);
   height: $height;
   position: relative;
   background-repeat: no-repeat;
   background-attachment: fixed;
   background-size: cover;
   background-position: center center;

}
@mixin Center($top:50%,$left:50%)
{
    position: absolute;
    top:$top;
    left:$left;
    transform: translate(-50%, -50%);   

 
}


@mixin backToTop($backClr:#0078ff,$Clr:#FFF)
{
  position: fixed;
  display: none;
  background: $backClr;
  color: #fff;
  width: 44px;
  height: 44px;
  text-align: center;
  line-height: 44px;
  font-size:$manFontSize;
  border-radius: 50%;
  right: 15px;
  bottom: 15px;
  transition: background 0.5s;
  z-index: 11;
}