/*start  Services*/
.services
{
   padding-top: 64px;
   .text-info
   {
      h3
      {
         font-size: 3rem;
         font-weight: bold;
         text-transform: uppercase;
         color:#1e1e1e;
      }
      p
      {
         color:#4e4e4e;
         &:after
         {
            content:'';
            display: block;
            width:40px;
            height: 5px;
            background-color: #0078ff;
            margin: 15px auto;
            margin-bottom: 50px !important;
         }
      }
   }
   .service-box 
   {
      background-color:#fff;
      padding: 2.5rem 1.3rem;
      border-radius: 1rem;
      margin-bottom: 3rem;
    
      .ico-circle
      {
         height: 100px;
         width: 100px;
         border-radius: 50%;
         line-height: 125px;
         margin: 0 auto;
         text-align: center;
         box-shadow: 0 0 0 10px #0078ff;
         display: block;
      }
      .s-title
      {
         font-size: 1.4rem;
         text-transform: uppercase;
         text-align: center;
         padding-top: 25px;
      }
      &:hover .ico-circle
      {
            transition: all 500ms ease;
            color:#fff;
            background-color:  #0078ff;
            box-shadow: 0 0 0 10px#cde1f8;
            
      }
   }
}

/*start  Services*/

