/*Start*/
.back-to-top
{
   @include backToTop();
   &:hover
   {
      color:#FFF;
   }

}
#preloader 
{
  
   position:fixed;
   z-index: 99999;
   top:0;
   bottom: 0;
   right: 0;
   left: 0;
   background: #fff;
   
   &:after
   {
      content:'';
      width:120px;
      height: 120px;
      position: absolute;
      top:calc(50% - 30px);
      left:calc(50% - 30px);
      border:6px solid #f2f2f2;
      border-radius: 50%;;
      animation: animate-preloader 500ms linear infinite
   }

   @keyframes animate-preloader 
   {
     0% {
       -webkit-transform: rotate(0deg);
       transform: rotate(0deg);
       border-top-color: #0078ff;
     }
     
     25%{

      border-top-color: green;
     }

     50%{

      border-top-color: yellow;
     }

     75%{

      border-top-color: red;
     }
     
     100% {
       -webkit-transform: rotate(360deg);
       transform: rotate(360deg);
       border-top-color: #0078ff;
     }
   }
}
/*End*/ 